import { INPUT_SUBMIT, ITEM_DELETE } from './action'

/**
 * state
 * actionTypestate
 * Object.assign,
 * key,
 * ,state
 */

let num = -1

// {
//   name: '',
//   ads: '',
//   phone: '861312312321',
//   email: 'djsk@163.com',
//   key: 0
// }

const initialState = {
  userInfo: []
}

export default function reducer(state=initialState, action) {
  switch(action.type) {
    case INPUT_SUBMIT:
      if (!action.value.name || !action.value.ads || !action.value.phone || !action.value.email) {
        return state
      }
      num++;
      return Object.assign(
        {},
        state,
        {userInfo: [...state.userInfo, {
          name: action.value.name,
          ads: action.value.ads.reduce((pre, next) => (pre+next)),
          phone: action.value.prefix + action.value.phone,
          email: action.value.email,
          key: num
          }]
        }
      )
    case ITEM_DELETE:
      let i = state.userInfo.findIndex(value => value.key === action.index)
      return Object.assign(
        {},
        state,
        {userInfo: [
          ...state.userInfo.slice(0, i),
          ...state.userInfo.slice(i+1)
        ]}
      )
    default:
      return state
  }
}


